import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import Search from "@/components/Search.vue"
Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: function(h) {
    return h(App);
  }
}).$mount("#app");

new Vue({
  router,
  store,
  render: function(h) {
    return h(Search);
  }
}).$mount("#search");